import stylo from '../img/stylo.png'
import korzina from '../img/korzina.png'


const state={
    todo:[
        {title:'My To-Do List', icon:stylo, todoItem:[]}
    ],
    idTask:0,
    funcs:{
        addTask:(value)=>{
            state.todo[0].todoItem.push({id:state.idTask++, pic:korzina,title:value})
            renderEntireTree(state)

        },
        deleteTask:(idTask)=>{
            state.todo[0].todoItem.splice(idTask,1)
            renderEntireTree(state)
            state.idTask--

        }
    }
}

export let renderEntireTree=()=>{

}
export let subscribe=(observer)=>{
    renderEntireTree=observer
}
// state.functions.showPopup()

window.state=state
export default state