import './Todo.css'
import Header from '../Header/Header'
import Input from '../Input/Input'
import TodoItem from '../TodoItem/TodoItem'

const Todo=(props)=>{
    return(
        <div className="todo">
            <Header
            todo={props.todo}/>
            <Input 
            funcs={props.funcs}
            />
            <ul className="menu">
            {props.todo.todoItem.map(todoItem=>{
                return <TodoItem
                state={props.state}
                        todoItem={todoItem}
                        funcs={props.funcs}
                        />
            })}
            </ul>
        </div>
    )
}
export default Todo