import './Input.css'
import React from 'react'


const Input=(props)=>{
    let input=React.createRef()
    function addTask(e){
        e.preventDefault();
        props.funcs.addTask(input.current.value)
        input.current.value=''
    }
    return(
        <div className="input">
            <form action="post">
            <input ref={input}  type="text" />
            <button onClick={addTask} ></button>
            </form>
        </div>
    )
}

export default Input