import './TodoItem.css'
import React from 'react'

const TodoItem=(props)=>{
    const pic=React.createRef()
    function deleteTask() {
        props.funcs.deleteTask(props.state.todo[0].todoItem.indexOf(props.todoItem))
        
    }
    function showKorzina(e) {
        if(e.target){
            pic.current.classList.add('on')
        }
    }
    function hideKorzina(e){
        if(!e.targer){
            pic.current.classList.remove('on')
        }
    }
    return(
        <li onClick={deleteTask} onMouseOver={showKorzina} onMouseOut={hideKorzina} className="todoItem">
            <div ref={pic} className="todoItem__pic"><img src={props.todoItem.pic} alt=""/></div>
            <div className="todoItem__descr">{props.todoItem.title}</div>
        </li>

    )
}

export default TodoItem