import React from 'react';
import Todo from './Components/ToDo/Todo';
import './App.css'



function App(props) {
  return (
    <div className="App">
      {props.stateProps.todo.map(todo=>{
        return <Todo
        state={props.stateProps}
        funcs={props.stateProps.funcs}
        todo={todo}
        />
      })}
    </div>
  );
}

export default App;
